@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
/* Buttons general style */
.button {
  font-weight: 500;
  font-size: 11px;
  line-height: 1.2;
  border: none;
  border-radius: 4px;
  transition: all, 300ms ease;
  padding: 4px 14px;
  text-transform: uppercase;
}

.button:hover {
  cursor: pointer;
}
button:disabled {
  color: var(--white);
  background: var(--disabled);
}

button:disabled:hover {
  background: var(--disabled);
  cursor: not-allowed;
}

.button__primary {
  background: var(--accent);
  color: var(--white);
}

.button__primary:hover {
  background: var(--accent-darker);
}

.button__danger {
  background: var(--danger);
  color: var(--white);
}

.button__danger:hover {
  background: var(--danger-darker);
}

.game__form {
  display: flex;
  margin-bottom: 40px;
}

.game__form__select {
  min-width: 180px;
  border-radius: 0;
  margin-right: 10px;
  font-size: 12px;
  line-height: 1.2;
  height: 25px;
}

.field__wrap {
}

.squares__table {
  border-collapse: collapse;
}

.squares__table tbody tr td {
  text-align: center;
  width: 50px;
  height: 50px;
  border: 1px solid #000;
  transition: all 300ms ease-in;
}

.squares__table tbody tr td:hover {
  background-color: var(--accent);
  color: #fff;
  cursor: pointer;
}

.history {
  margin-top: -7px;
  height: 500px;
  overflow-y: auto;
  padding-right: 15px;
  margin-left: 43px;
}

.history__list {
  margin-top: 10px;
}

.history__alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  color: #856404;
  background-color: #fff3cd;
  border-color: #ffeeba;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul {
  list-style: none;
}

:root {
  --accent: #0069d9;
  --accent-darker: #0562c5;
  --white: #fff;
  --disabled: #6c757d;
  --danger: #dc3545;
  --danger-darker: #c82333;
}

.container {
  width: 1110px;
  margin: 0 auto;
}

@media all and (max-width: 1199px) {
  .container {
    width: 960px;
  }
}

@media all and (max-width: 991px) {
  .container {
    width: 750px;
  }
}

body {
  font-family: Poppins, sans-serif;
}

.app {
  display: flex;
  margin-top: 20px;
}

